import React, { Component } from 'react';
// import  'react-router-dom';
// import  'react-router';
import { BrowserRouter, Link, Route, Switch } from 'react-router-dom';



import Home from './tabs/Home';
import Skilles from './tabs/Skilles';
import Info from './tabs/Info';
import Exp from './tabs/Exp';
import Contact from './tabs/Contact';

let Slide = true ;

class Main extends Component {
    state = {  }
    slide = () => {
        if (Slide) {
            Slide = false ;
            this.doSlide();
        } else {
            Slide = true ;
            this.dontSlide();
        }
    }
    slideBack =() => {
        if ( window.innerWidth < 800 ){
            this.doSlide();
        }
    }
    doSlide = () => {
        document.querySelector(".btns").style.transform = "translateX(-80px)" ;
    }
    dontSlide = () => {
        document.querySelector(".btns").style.transform = "translateX(0px)" ;
    }
    render() { 
        return (  
            <div className="main" >
                <BrowserRouter>
                <div className="btns" id="Btns">
                    <div className="btns2">
                    <div className="highlite"></div>
                    <div onClick={this.slide} className="slider"></div>
                        <div className="ul">
                            <Link className="link" onClick={this.slideBack} to="/Home">Home</Link>
                            <Link className="link" onClick={this.slideBack} to="/Skilles">Skilles</Link>
                            <Link className="link" onClick={this.slideBack} to="/Info">Info</Link>
                            <Link className="link" onClick={this.slideBack} to="/Exp">Exp</Link>
                            <Link className="link" onClick={this.slideBack} to="/Contact">Contact</Link>
                        </div>
                    </div>
                </div>
                <div className="box">
                    <Switch>
                        <Route path="/" component={Home} exact/>
                        <Route path="/Home" component={Home} exact/>
                        <Route path="/Skilles" component={Skilles} exact/>
                        <Route path="/Info" component={Info} exact/>
                        <Route path="/Exp" component={Exp} exact/>
                        <Route path="/Contact" component={Contact} exact/>
                    </Switch>
                </div>
                </BrowserRouter>
                
            </div>
        );
    }
}
//Start the router --done
//Piece of cake --done

//make the nav responsive

//make the "skilles" using grid
export default Main;
